.list-container {
  overflow-x: hidden;
  overflow-y: scroll;
  margin-top: 64px;
  -webkit-overflow-scrolling: touch;
}
.list-container .app-main {
  width: 100%;
  max-width: 800px;
  margin: 30px auto;
}
.list-container .app-main .app-main-items {
  background-color: #fff;
  border-radius: 2px;
  font-size: 12px;
  border: 1px solid #e9e9e9;
  margin: 0 15px 30px 15px;
}
.list-container .app-main .app-main-items .app-main-items-head {
  height: 48px;
  line-height: 48px;
  border-bottom: 1px solid #e9e9e9;
  padding: 0 24px;
}
.list-container .app-main .app-main-items .app-main-items-head .app-main-items-head-title {
  font-size: 14px;
  color: rgba(0,0,0,0.85);
  font-weight: 500;
}
.list-container .app-main .app-main-items .app-main-items-body {
  padding: 2px;
  margin-right: -1px;
}
.list-container .app-main .app-main-items .app-main-items-body:after {
  content: '';
  display: block;
  clear: both;
}
.list-container .app-main .app-main-items .app-main-items-body .app-main-items-card {
  width: 33.33%;
  float: left;
  position: relative;
  z-index: 1;
  cursor: pointer;
}
.app-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  height: 64px;
  background-color: currentColor;
  -webkit-box-shadow: 0 1px 1px 0 currentColor, 0 2px 1px -2px currentColor, 0 1px 3px 0 currentColor;
          box-shadow: 0 1px 1px 0 currentColor, 0 2px 1px -2px currentColor, 0 1px 3px 0 currentColor;
  -webkit-transition: background-color 300ms, color 300ms;
  transition: background-color 300ms, color 300ms;
}
.app-header .app-header-title {
  font-weight: 400;
  font-size: 20px;
  line-height: 64px;
  position: relative;
  z-index: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-left: 20px;
  font-weight: bold;
}
.app-header span {
  position: absolute;
  right: 20px;
  display: block;
  text-align: right;
  width: 200px;
  line-height: 26px;
  z-index: 10;
}
.app-header span.app-header-title-hex {
  top: 8px;
}
.app-header span.app-header-title-rgb {
  bottom: 8px;
}
.app-main-item {
  padding: 20px 0;
  text-align: center;
  color: #fff;
  background-color: currentColor;
  border-radius: 4px;
  margin: 2px;
  border: 1px solid currentColor;
  -webkit-transition: all 150ms cubic-bezier(0, 0, 0.21, 1);
  transition: all 150ms cubic-bezier(0, 0, 0.21, 1);
  position: relative;
}
.app-main-item:after {
  content: 'Copied!';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  text-align: right;
  line-height: 150px;
  color: currentColor;
  -webkit-transition: all 0.3s cubic-bezier(0.18, 0.89, 0.32, 1.28);
  transition: all 0.3s cubic-bezier(0.18, 0.89, 0.32, 1.28);
  opacity: 0;
}
.app-main-item:hover {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  -webkit-box-shadow: 0 0 0.5rem #eee;
          box-shadow: 0 0 0.5rem #eee;
  border-color: #eee;
}
.app-main-item.copied:after {
  opacity: 1;
  right: 10px;
}
.app-main-item p {
  padding: 5px 0;
  font-weight: bold;
}
.app-main-item p.app-main-item-name {
  font-size: 14px;
}
footer {
  padding: 50px;
  color: #999;
  text-align: center;
}
footer a,
footer svg {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
footer a {
  color: #999;
}
footer a:hover {
  color: #333;
}
footer svg {
  fill: #999;
}
footer svg:hover {
  fill: #333;
}